.main{
    min-height: 100vh;
    display: flex;
    // align-items: center;
    // justify-content: center;
    // padding: 0 15px;
    min-height: 100vh;
    flex-direction: column;
    background-color: #000000;
    &-block{
        display: flex;
        width: 100%;
        height: 768px;
        background: url(../../graphics/images/bg.svg) no-repeat top center / cover;
        text-align: center;
        align-items: center;
        justify-content: center;
        &-text{
            width: 100%;
            margin-bottom: 114px;
            display: flex;
            flex-direction: column;
            align-items: center;
            &_title{
                transition: opacity 0.5s;
                font-size: 288px;
                color: #FFC94B;
            }
            &_sub{
                // position: relative;
                
                max-width: 1000px;
                &_subtitle{
                    text-align: justify;
                    text-transform: uppercase;
                }
            }
        }
        &-mobile{
            display: none;
        }
    }
    &-links{
        width: 100%;
        height: 192px;
        align-items: center;
        justify-content: center;
        display: flex;
        &_text{
            
            padding-left: 15px;
            &-title{
                font-size: 24px;
            }
            &-title:hover{
                color:#FFC94B;
                font-size: 32px;
                transition: opacity 1s;
            }
        }
        &_text:first-child{
            padding-left: 0px;
        }
    }
}
@media (max-width: 1024px) {
    .main{
        &-block{
            &-text{
                &_title{
                    font-size: 240px;
                }
                &_sub{
                    max-width: 800px;
                    &_subtitle{
                    }
                }
            }
            &-mobile{
                color:#FFC94B;
                display: none;

            }
        }
        &-links{
            display: none;
            &_text{
                &-title{
                }
                &-title:hover{
                }
            }
            &_text:first-child{
            }
        }
    }
}

