* {
    // background-color: #000000;
    font-family: Inter;
    font-size: 24px;
    font-weight: 700;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: white;
    &::-webkit-scrollbar {
      width: 0;
    }
  }
  
  
  a {
    text-decoration: none;
  }
  
  img {
    display: block;
    user-select: none;
    pointer-events: none;
  }